import React, { useContext, useRef, useState } from "react";
import '../Message.css'
import { CommentBox } from "../../commentBox/CommentBox";
import { Subcommentbox } from "../../commentBox/Subcommentbox/Subcommentbox";


const showReply = React.createContext()

export function useOpenReply(){
    return useContext(showReply)
}

function Submessage(props) {
    const likeIcon = useRef()
    const numLikes = useRef()

 
    const[openReply, setOnpenReply] = useState(false)

    //toggled when cancel and reply buttn are pressed

    const changeOpenReply =()=>{
        setOnpenReply(preveState => preveState = !preveState)
    }


   
    // like message
    let toggleLike = false
    let likes = props.likes
    const likeComment = () =>{
        toggleLike = !toggleLike
        if(toggleLike){
            likes++
            likeIcon.current.style.color = "#4688de"
        }else{
            likes--
            likeIcon.current.style.color = "gray"
        }

        numLikes.current.innerHTML = likes
    }

    const deleteMessage = () =>{

    }

  return (
    <>
      <section className="messageContainer">
          <div className="messageUser">{props.user}</div>
          <i className="fas fa-user-circle"></i>
          <div className="messageText">{props.message}</div>

          <section className="messageIconsContainer">
              <i className="fas fa-thumbs-up" ref={likeIcon} onClick={likeComment}></i>
              <dvi ref={numLikes}>{props.likes}</dvi>
              <i className="fas fa-thumbs-down"></i>
              {
                  !props.editable ? (
                    <div onClick={changeOpenReply} style={{cursor:"pointer"}}>REPLY</div>
                  ): (
                    <div onClick={deleteMessage} style={{cursor:"pointer"}}>DELETE</div>
                  )
              }
          </section>

          <showReply.Provider value={changeOpenReply}>
              {openReply && <Subcommentbox autoFocus={true} />}
          </showReply.Provider>
         
      </section>
    </>
  );
}

export default Submessage;
