import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import { PostCompontent, FeedCompontent } from './post';
import { PostCompontentDetail } from './post/Compontents';
import { UserProfileBadge } from './profile';
import { Comment } from './comments';
import reportWebVitals from './reportWebVitals';

const e = React.createElement
const rootEl = document.getElementById('root')
if(rootEl){
  ReactDOM.render(e(PostCompontent, rootEl.dataset), rootEl);
}

const rootElFeed = document.getElementById('root-feed')
if(rootElFeed){
  ReactDOM.render(e(FeedCompontent), rootElFeed);
}

const postDetailEl = document.querySelectorAll(".post-detail-el")
postDetailEl.forEach(container=>{
  if(postDetailEl){
    ReactDOM.render(e(PostCompontentDetail, container.dataset), container)
  }
})

const postProfileEl = document.querySelectorAll(".post-profile-el")
postProfileEl.forEach(container=>{
  if(postProfileEl){
    ReactDOM.render(e(UserProfileBadge, container.dataset), container)
  }
})


const rootCommentEl = document.getElementById('root-comment')
if(rootEl){
  ReactDOM.render(e(Comment, rootCommentEl.dataset), rootCommentEl);
}

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
