function getCookie(name) {
    var cookieValue = null;
    if (document.cookie && document.cookie !== '') {
        var cookies = document.cookie.split(';');
        for (var i = 0; i < cookies.length; i++) {
            var cookie = cookies[i].trim();
            // Does this cookie string begin with the name we want?
            if (cookie.substring(0, name.length + 1) === (name + '=')) {
                cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
                break;
            }
        }
    }
    return cookieValue;
}


export function loadPost(username, callback, nextUrl){
    let url = 'http://127.0.0.1:8000/api/post/'


    if(username){
        url = `http://127.0.0.1:8000/api/post/?username=${username}`
    }
    const xhr = new XMLHttpRequest()

    const method = "GET"
   

    xhr.responseType = "json"
    xhr.open(method, url)
    xhr.setRequestHeader("Content-Type", "application/json")
    

    xhr.onload = function(){
        if(xhr.status===403){
            const detail = xhr.response.detail
            if(detail === "Authentication credentials were not provided."){
                window.location.href = "/accounts/login?showLoginRequired=true"
            }
        }
      callback(xhr.response, xhr.status)
    }
    xhr.send()
  }

export function createPost(data, callback){

    let jsonData

    if(data){
        jsonData = data
    }
    const xhr = new XMLHttpRequest()
    const csrftoken = getCookie('csrftoken');
    const method = "POST"
    const url = 'http://127.0.0.1:8000/api/post/create/'
    xhr.responseType = "json"
    xhr.open(method, url)
 
    if(csrftoken){
        xhr.setRequestHeader("X-CSRFToken", csrftoken)
        xhr.setRequestHeader("HTTP_X_REQUESTED_WITH", 'XMLHttpRequest')
        xhr.setRequestHeader("Http-Requested-With", 'XMLHttpRequest')

    }
    
    xhr.onload = function(){
        if(xhr.status===403){
            const detail = xhr.response.detail
            if(detail === "Authentication credentials were not provided."){
                window.location.href = "/accounts/login?showLoginRequired=true"
            }
        }
        callback(xhr.response, xhr.status)
    }
    xhr.send(jsonData)
}



