import React, { useState } from "react";
import "./commentBox.css";
import { useOpenReply } from "../message/submessage/Message";

export function CommentBox(props) {

  const changeOpenReply = useOpenReply()

  const message = React.useRef()

  const [showCommentLine, setCommentLine] = useState(false)
  const [showButtons, setShowButtons] = useState(false)
  const [enableButtons, setEnambleButtons] = useState(true)

  const commentFocus = () =>{
    setCommentLine(true)
    setShowButtons(true)
  }

  const commentFocusOut = () =>{
    setCommentLine(false)
  }

  const commentStroke = event =>{
    let currmessage = event.target.value
    if(currmessage){
      setEnambleButtons(false)
    }else{
      setEnambleButtons(true)
    }
  }

  const sendComment = (event) =>{
    event.preventDefault()
    console.log(event)
  }

  return (
    <div>
      <form>
        <section className="commentBox">
          <textarea
            rows={1}
            autoFocus={props.autoFocus}
            placeholder="Add a public comment..."
            ref={message}
            onFocus={commentFocus}
            onBlur={commentFocusOut}
            onKeyUp={commentStroke}
          />
        {showCommentLine && <div className="commentLine"></div>}
        </section>

        {showButtons && (
          <>
          <button className="commentButton sendButton" disabled={enableButtons} onClick={sendComment}>COMMENT</button>
          <button className="commentButton" style={{color:"gray", backgroundColor: "transparent"}} onClick={()=>{
            setShowButtons(false)
            changeOpenReply()
          }}>CANCEL</button>
          </>
        )}
      </form>
    </div>
  );
}
