import React, {useEffect, useState} from 'react'
import { loadProfile, createFollowBtn } from '../lookup'
import {  UserPicture } from '../post/Compontents'

function ProfileBadge(props){

    const {user, didfollowToggle, profileLoading} = props   
    let currentVerb = (user && user.is_following) ? "unfollow":"follow"
    const className = currentVerb === "follow" ? "btn btn-primary": "btn btn-danger"
    


    const handleFollowToggle =(event)=>{
        event.preventDefault()
        if(didfollowToggle && !profileLoading){
            didfollowToggle(currentVerb)
        }
        
    }

    return(
        user ? <div class="container rounded bg-white mt-5 mb-5">
            <div class="row">
                <div class="col-md-3 border-right">
                    <div class="d-flex flex-column align-items-center text-center p-3 py-5"><span className="mb-3"><UserPicture className="fw-5 " user={user} /></span><span className="font-weight-bold ">{user.username}</span><span class="text-black-50">{user.email}</span><span> </span></div>
                </div>
                <div class="col-md-5 border-right">
                    <div class="p-3 py-5">
                        <div class="d-flex justify-content-between align-items-center mb-3">
                            <h4 class="text-right">Profile Detail</h4>
                        </div>
                        <div class="row mt-2">
                            <div class="col-md-6"><label>First Name: <b>{user.first_name}</b></label></div>
                            <div class="col-md-6"><label>Last Name: <b>{user.last_name}</b></label></div>
                        </div>
                        <div class="row mt-3">
                            <div class="col-md-12"><label>Username:  <b>{user.username}</b> </label></div>
                            <div class="col-md-12"><label>Location  <b>{user.location}</b> </label></div>
                            <div class="col-md-12"><label>Phone number:  <b>{user.phone_number}</b> </label></div> <br /><br />
                            <div class="col-md-12"><p>{user.bio}</p></div>
                        </div>
                        <div class="row mt-3">
                            <div class="col-md-6"><label>Follwers: <b>{user.followers}</b></label></div>
                            <div class="col-md-6"><label>Following: <b>{user.following}</b> </label></div>
                        </div>
                    </div>
                </div>
                <div class="col-md-4">
                    <div class="p-3 py-5">
                        <div class="d-flex justify-content-between align-items-center experience"><span className={className} onClick={handleFollowToggle}><i class="fa fa-plus"></i>&nbsp;{currentVerb}</span></div><br/>
                        {/* <div class="col-md-12"><label>Experience in Designing</label><input type="text" class="form-control" placeholder="experience" value=""/></div> <br/>
                        <div class="col-md-12"><label>Additional Details</label><input type="text" class="form-control" placeholder="additional details" value=""/></div> */}
                    </div>
                </div>
            </div>
        </div>:null
    )   
}

export function UserProfileBadge(props){
    const {username} = props
    const [profile, setProfile] = useState(null)
    const [didProfile, setDidProfile] = useState(false)
    const [profileLoading, setProfileLoading] = useState(false)

    const myCallback = (response, status) =>{
        if(status === 200){
            setProfile(response)
        }
    }

    useEffect(()=>{
        if (didProfile === false){
            loadProfile(username, myCallback) 
            setDidProfile(true)
        }  
      }, [username, didProfile, setDidProfile])

    const handleDidfollowToggle =(currentAction)=>{
        console.log(currentAction)
        createFollowBtn(username, {"action": currentAction}, (response, status)=>{
            console.log(response, status)
            if(status===200){
                setProfile(response)
            }
            setProfileLoading(false)
        })
       setProfileLoading(true)

    }
    
    
    return <ProfileBadge user={profile} didfollowToggle={handleDidfollowToggle} profileLoading={profileLoading}/>

}